.page__header nav {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background: #f9f9f9;
    box-shadow: 0 25px 30px -25px rgba(0, 0, 0, 0.17);
    position: relative;
    z-index: 99;
  }
  .page__header nav .brand .page__title {
    margin: 0;
    letter-spacing: -0.07em;
    font-size: 27px;
    text-align: right;
    color: rgba(0, 0, 0, 0.87);
    position: relative;
    top: 2px;
  }
  .page__header nav .brand .page__title small {
    font-weight: 300;
    font-size: 12px;
    background: #ff5500;
    /* border-radius: 50px; */
    padding: 0.3em 1em 0.25em;
    color: white;
    margin-left: 0.9em;
    position: relative;
    top: 12px;
    letter-spacing: -0.01em;
    border-radius: 2px;
  }
  .page__header nav .navbar__drawer-toggle {
    background: transparent;
    border: 0;
    padding: 1.5em 0;
    margin: 0;
    outline: 0;
  }
  .page__header nav .navbar__drawer-toggle .menu-icon-lines {
    margin-left: auto;
    direction: rtl;
    width: 35px;
    cursor: pointer;
  }
  .page__header nav .navbar__drawer-toggle .menu-icon-lines span {
    display: block;
    width: 30px;
    height: 4px;
    background: #ff5500;
    border-radius: 50px;
    transition: all 600ms ease;
  }
  .page__header nav .navbar__drawer-toggle .menu-icon-lines span:not(:last-child) {
    margin-bottom: 0.35rem;
  }
  .page__header nav .navbar__drawer-toggle .menu-icon-lines span:nth-child(1) {
    width: 14px;
  }
  .page__header nav .navbar__drawer-toggle .menu-icon-lines span:nth-child(1):after {
    left: -19px;
  }
  .page__header nav .navbar__drawer-toggle .menu-icon-lines span:nth-child(2) {
    width: 8px;
  }
  .page__header nav .navbar__drawer-toggle .menu-icon-lines span:nth-child(2):after {
    left: -13px;
  }
  .page__header nav .navbar__drawer-toggle .menu-icon-lines span:nth-child(3) {
    width: 22px;
  }
  .page__header nav .navbar__drawer-toggle .menu-icon-lines span:nth-child(3):after {
    left: -27px;
  }
  .page__header nav .navbar__drawer-toggle .menu-icon-lines span:after {
    content: "";
    display: block;
    width: 4px;
    height: 4px;
    background: #ff5500;
    border-radius: 50%;
    position: relative;
    left: -30px;
    transition: all 400ms ease;
  }
  .page__header nav .navbar__drawer-toggle .menu-icon-lines:hover span {
    width: 30px;
  }
  .page__header nav .navbar__drawer-toggle .menu-icon-lines:hover span:nth-child(1):after {
    width: 16px;
    border-radius: 50px;
    left: 0px;
  }
  .page__header nav .navbar__drawer-toggle .menu-icon-lines:hover span:nth-child(2):after {
    width: 22px;
    border-radius: 50px;
    left: 0;
  }
  .page__header nav .navbar__drawer-toggle .menu-icon-lines:hover span:nth-child(3):after {
    width: 8px;
    border-radius: 50px;
    left: 0;
  }
  .page__header nav .navbar__drawer {
    display: none;
    position: absolute;
    width: 100%;
    bottom: -67px;
    background: white;
    left: 0;
  }
.hero-intro {
  background: linear-gradient(45deg, #5B544E, #A49087);
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  display: flex;
  justify-content: space-between;
  background-size: cover;
  background-position: 50% 0;
}
.hero-intro__right .open-dates {
  margin-bottom: 2em;
}
.hero-intro__right .open-dates p {
  margin: 0;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.5;
  position: relative;
  display: inline-block;
  z-index: 1;
}
.hero-intro__right .open-dates p:last-child {
  color: rgba(255, 255, 255, 0.7);
}
.hero-intro__right .open-dates p:after {
  content: "";
  display: block;
  width: 100%;
  background: rgba(166, 166, 166, 0.5);
  border-radius: 50px;
  position: absolute;
  height: 9px;
  top: 15px;
  z-index: -1;
}
.hero-intro__right .museum-shop-link {
  color: white;
  text-decoration: none;
  padding: 1em 2em;
  font-weight: 500;
  font-size: 15px;
  background: #ff5500;
  transition: all 200ms ease;
  position: relative;
  border-radius: 50px;
  letter-spacing: 0.04em;
}
.hero-intro__right .museum-shop-link:hover {
  background: #222;
  color: white;
}
.hero-intro__right blockquote {
  font-size: 17px;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.8);
  z-index: 1;
  position: relative;
}
.hero-intro__right blockquote p {
  margin: 0;
  font-size: 14px;
  width: 180px;
  position: relative;
}
.hero-intro__left img {
  /* width: 340px;
  height: 320px; */
  width: 100%;
  object-fit: cover;
  object-position: 50% 50%;
  border-radius: 61% 39% 64% 36%/40% 64% 36% 60%;
  box-shadow: 0 15px 60px -20px black;
  border: 10px solid #dcd1c3;
  mix-blend-mode: overlay;
}

/* Extra small devices (portrait phones, less than 576px) */
@media (max-width: 575.98px) { 
.hero-intro__right blockquote, .hero-intro__right .open-dates p {
  font-size: 16px;
}
.hero-intro__right .museum-shop-link {
  font-size: 14px;
  /* padding: 1em 4.2em 1em 2em; */
}
}
