  /* Submit Button */
  button.button-morph{
    display:inline-block;
    text-decoration:none;
    color:#ff7b3e;
    background-color:transparent;
    border:1px solid #ff7b3e;
    border-radius:100px;
    padding: .3em 1.2em;
    font-size: 18px;
    background-size: 200% 100%;	
    background-image: linear-gradient(to right, transparent 50%, #ff7b3e 50%);
    transition: background-position .3s cubic-bezier(0.19, 1, 0.22, 1) .1s, color .5s ease 0s, background-color .5s ease;
    cursor: pointer;
  }
  button.button-morph:hover{
    color:rgba(255, 255, 255, 1);
    background-color: #ff7b3e;
    background-position: -100% 100%;
    cursor: pointer;
  }
  button.button-morph::after{
    content: attr(data-text);
    font-family: "Material Icons";
  }
  button.button-morph:focus {outline:0 !important;}
.loading {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
       background: #302c2ac5;
    /* opacity: .97; */
    z-index: 9999;
  }

  .centered {
    position: absolute;
	 top: 0;
	 bottom: 0;
	 left: 0;
	 right: 0;
	 margin: auto;
	 text-align: center;
  }
  #smileyFace{
    width: 100px;
    height: 100px;
    position: absolute;
  }
  #smileyFace p{
    text-align: center;
    font-size: 1.3em;
    color:  orange;
    position: relative;
    top: 25px;
    /* left:  10px; */
    -webkit-animation-name: hide;
            animation-name: hide;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
  }
  .mouth{
    position: absolute;
    left: -15px;
    width: 130px;
    height: 65px;
    border: 10px solid orange;
    border-bottom: none;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    border-top-left-radius: 150px;
    border-top-right-radius: 150px;
  }
  .left{
    -webkit-transform-origin: center bottom;
            transform-origin: center bottom;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
   -webkit-animation-name: spin_left;
           animation-name: spin_left;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
  }
  .right{
    -webkit-transform-origin: center bottom;
            transform-origin: center bottom;
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    -webkit-animation-name: spin_right;
            animation-name: spin_right;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
  }
  
  .eyes{
    opacity: 0;
    -webkit-animation-name: show;
            animation-name: show;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
  }
  .eyes:before, .eyes:after{
    position: absolute;
    content:" ";
    padding: 6px 6px;
    background-color: orange;
    border-radius: 10px;
  }
  .eyes:before{
    top: 35px;
    left: 5px;
  }
  .eyes:after{
    top:35px;
    left: 85px;
  }
  @-webkit-keyframes spin_left{
      0% {
          -webkit-transform:rotate(-90deg);
                  transform:rotate(-90deg);
      }
      80% {
          -webkit-transform:rotate(540deg);
                  transform:rotate(540deg);
      }
      100% {
          -webkit-transform:rotate(540deg);
                  transform:rotate(540deg);
      }
  }
  @keyframes spin_left{
      0% {
          -webkit-transform:rotate(-90deg);
                  transform:rotate(-90deg);
      }
      80% {
          -webkit-transform:rotate(540deg);
                  transform:rotate(540deg);
      }
      100% {
          -webkit-transform:rotate(540deg);
                  transform:rotate(540deg);
      }
  }
  @-webkit-keyframes spin_right{
      0% {
          -webkit-transform:rotate(90deg);
                  transform:rotate(90deg);
      }
      80% {
          -webkit-transform:rotate(540deg);
                  transform:rotate(540deg);
      }
      100% {
          -webkit-transform:rotate(540deg);
                  transform:rotate(540deg);
      }
  }
  @keyframes spin_right{
      0% {
          -webkit-transform:rotate(90deg);
                  transform:rotate(90deg);
      }
      80% {
          -webkit-transform:rotate(540deg);
                  transform:rotate(540deg);
      }
      100% {
          -webkit-transform:rotate(540deg);
                  transform:rotate(540deg);
      }
  }
  @-webkit-keyframes show{
    0%{
      opacity: 0;
    }
    40%{
      opacity: 0;
    }
    80%{
      opacity: 1;
    }
    100%{
      opacity: 1;
    }
  }
  @keyframes show{
    0%{
      opacity: 0;
    }
    40%{
      opacity: 0;
    }
    80%{
      opacity: 1;
    }
    100%{
      opacity: 1;
    }
  }
  @-webkit-keyframes hide{
    0%{
      opacity: 1;
    }
    40%{
      opacity: 1;
    }
    66%{
      opacity: 0;
    }
    100%{
      opacity: 0;
    }
  }
  @keyframes hide{
    0%{
      opacity: 1;
    }
    40%{
      opacity: 1;
    }
    66%{
      opacity: 0;
    }
    100%{
      opacity: 0;
    }
  }
